@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-ibm-plex-sans: "IBM Plex Sans";

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-sm: 14px;
  --font-size-5xl: 24px;
  --font-size-smi: 13px;
  --font-size-xs: 12px;
  --font-size-lg: 18px;
  --font-size-lgi: 19px;
  --font-size-3xl: 22px;
  --font-size-base: 16px;
  --font-size-2xs: 11px;
  --font-size-mid: 17px;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: #8a8a8a;
  --color-gray-200: #88898a;
  --color-gray-300: #808080;
  --color-gray-400: #212529;
  --color-whitesmoke-100: #f7f8fa;
  --color-whitesmoke-200: #f1f3f5;
  --color-whitesmoke-300: #f2f2f2;
  --color-whitesmoke-400: #edeef0;
  --color-gainsboro-100: #e0e0e0;
  --color-gainsboro-200: #d9d9db;
  --color-royalblue: #2f6ce5;
  --color-black: #000;
  --color-darkslategray-100: #495057;
  --color-darkslategray-200: #3d3d3d;
  --color-darkslategray-300: #343a40;
  --color-darkslategray-400: #2e2e2e;
  --color-seagreen: #008a45;
  --color-honeydew: #effff4;
  --color-silver-100: #b8b8b8;
  --color-silver-200: #b6b6b8;
  --color-dimgray-100: #6a6a6b;
  --color-dimgray-200: #5c5c5c;
  --color-dimgray-300: #525252;
  --color-darkgray-100: #a9aeb8;
  --color-darkgray-200: #989899;
  --color-tomato: #e56135;
  --color-mediumseagreen: #02b875;

  /* border radiuses */
  --br-11xs: 2px;
  --br-xl: 20px;
  --br-5xs: 8px;
  --br-9xs: 4px;
  --br-2xl: 21px;
  --br-sm: 14px;
  --br-5xl: 24px;
  --br-3xs: 10px;
}
